<template>
  <div class=" d-flex flex-column pb-2 pb-sm-3">
    <h1 class="text-center fs-6 fw-bold mb-3">Please login to continue</h1>
    <section class="bg-white border border-top-0 flex-grow-1">
      <Tabs />
      <form @submit.prevent="submitForm" class="bg-white  p-3 pb-0  mt-2">
        <Email v-model="formData.email"/>
        <div v-show="!getAuthState('signInModal')">
          <Password  v-model="formData.signUpPassword" autocomplete="new-password"/>     <!--ðŸ‘ˆ PASS PROP TO CHILD COMPONENT -->
          <RegisterCTA />
        </div><!-- Sign In -->

        <div v-show="getAuthState('signInModal')">
          <Password v-model="formData.signInPassword" autocomplete="current-password"/>     <!--ðŸ‘ˆ PASS PROP TO CHILD COMPONENT-->
          <LoginCTA />
        </div><!-- Sign Up -->
  
        <div :class="{invisible: !getAuthState('err')}" class="alert-danger alert d-flex flex-row-reverse justify-content-between align-items-center py-0 pe-0 pe-sm-1 my-1 mb-2 ">
          <button @click="closeAuthErr" class="btn p-1 pe-0 align-self-end py-0 " type="button" >
            <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-x border-0 fill-danger" viewBox="0 0 16 16">
              <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"/>
            </svg>
          </button>
          <span class="fs-xs-7 fs-6">{{ getAuthState('errMsg')}}</span> 
        </div>
      </form>
    </section>
  </div>
</template>


<script setup>
  // import DOMPurify from 'dompurify';
  import {reactive, onMounted, onBeforeUnmount} from 'vue'
  import {mapGetters, mapMutations, mapActions} from '@/libs/vuex'

  import Tabs from '@/components/misc/auth-tab-buttons.vue'
  import Email from '@/components/forms/email-input.vue'
  import Password from '@/components/forms/password-input.vue'
  import LoginCTA from  '@/components/misc/login-cta.vue'
  import RegisterCTA from  '@/components/misc/register-cta.vue'


  const {closeAuthErr, resetModal, enableScroll, disableScroll} = mapMutations()
  const {login, register, setErrMsg } = mapActions()
  const {getAuthState} = mapGetters()

  onMounted(()=> disableScroll())
  
  onBeforeUnmount(()=> {
    enableScroll()
    resetModal()
  })

  const formData = reactive({
    email: '',
    signUpPassword: '',
    signInPassword: ''
  })

  const submitForm = async() => {
    const email = DOMPurify.sanitize(formData.email)  //ðŸ‘ˆ sanitize user input using Dompurify
    const signInPwd = DOMPurify.sanitize(formData.signInPassword);
    const signUpPwd = DOMPurify.sanitize(formData.signUpPassword);
    
    const signInForm = getAuthState.value('signInModal') //ðŸ‘ˆ true = signIn form || false = signUp form

    if(signInForm && signInPwd.length > 0) await login({email, pwd:signInPwd})  //ðŸ‘ˆ checks if signIn form and the password > 0
    else if(!signInForm && signUpPwd.length>0) await register({email, pwd:signUpPwd}) //ðŸ‘ˆ else if signUp form and the password > 0
    else await setErrMsg('password required')   //ðŸ‘ˆpassword field empty show Err Msg
  }

</script>


<style scoped></style>

